import axios from 'axios'
import setAuthToken from '../utils/setAuthToken.js'


export const loadingTwo = (val, obj) => {
    obj.$store.state.loading2 = val
}

export const signUpAction = (data, obj) => {
    loadingTwo(true, obj)
    axios.post('/api/users/register', data)
        .then(result => {
            const { token, user } = result.data

            setAuthToken(token)  
            localStorage.setItem('jwtToken', token);

            obj.$router.push('/edit')
            loadingTwo(false, obj)
        })
        .catch(err => {
            console.log("Sign up action",err)
        })
}

export const editUserAction = (data, obj) => {
    loadingTwo(true, obj)
    axios.post('/api/users/edit', data)
        .then(({data}) => {
            setUserAction(data, obj)
            obj.$router.push('/home')
            loadingTwo(false, obj)
        })
        .catch(err => {
            console.log("Edit user action",err)
        })
}

export const setUserAction = (data, obj) => {
    if (data) {
        obj.$store.state.user.name = data.name
        obj.$store.state.user.location = data.location
        obj.$store.state.user.age = data.age
        obj.$store.state.user.email = data.email
        obj.$store.state.user.avatar = data.avatar
        obj.$store.state.user.infoUpdate = data.infoUpdate

        obj.$store.state.todos = data.todos

        return data.avatar
    }

    console.log("setting user action went wrong: " )
} 

export const loginUserAction = (data, obj) => {
    loadingTwo(true, obj)
    axios.post('/api/users/login', data) 
        .then(result => {
            const { token, user } = result.data

            localStorage.setItem('jwtToken', token)
            setAuthToken(token)  

            let imageFile = setUserAction(user, obj)

            if (obj.$store.state.user.infoUpdate) {
                obj.$router.push('/home')
            } else {
                obj.$router.push('/edit')
            }

            loadingTwo(false, obj)
            return imageFile
        })
        .catch(err => {
            console.log(err)
            UIkit.notification({
                message: err.response.data.message,
                status: 'danger',
                pos: 'bottom-center',
                timeout: 2000
            })
            loadingTwo(false, obj)
        })
}

export const getUserAction = (obj) => {
    axios.get('/api/users/profile')
        .then(result => {
            console.log("::", result.data)
        let imageFile = setUserAction(result.data, obj)
        return imageFile
        })
        .catch(err => {
            console.log("get user went wrong: ", err)
        })
}

export const logOutUser = (obj) => {
    localStorage.removeItem('jwtToken')
    setAuthToken(false);
    obj.$store.state.user.email = ''
    obj.$store.state.user.name = ''
    obj.$store.state.user.age = ''
    obj.$store.state.user.location = ''
    obj.$store.state.user.todos = ''
    obj.$store.state.user.avatar = ''
    obj.$router.push('/')
}