<template>
    <div class="loading-ct">
        <span class="uk-margin-small-right text-center ct-spinner uk-position-center" 
            uk-spinner="ratio: 4" v-if="checkLoading" ></span>
        <div class="row dashboard shadow" uk-grid v-else>
            <app-user-details :pic="profilePic"/>
            <app-panel></app-panel>
        </div>
    </div>
</template>

<script>
    import UserDetails from './child/UserDetails.vue'
    import Panel from './child/Panel.vue'
    import { getUserAction, logOutUser } from '../../apis/authentication.js'
    import setAuthToken from '../../utils/setAuthToken.js'
    import jwt_decode from 'jwt-decode'
    
export default {
    data() {
        return {
            loading: false,
            profilePic: this.$store.state.user.avatar
        }
    },
    components: {
        'app-user-details': UserDetails,
        'app-panel': Panel
    },
    computed: {
        checkLoading() {
            return this.$store.state.loading2
        }
    },
    beforeMount() {
        if (localStorage.jwtToken) {
            setAuthToken(localStorage.jwtToken);
            const decoded = jwt_decode(localStorage.jwtToken);
            console.log("router secure...")

            const currentTime = Date.now() / 1000;
            if (decoded.exp < currentTime) {
            // Logout user
            console.log("logging out user...")
            logOutUser(this)
            this.$router.push('/')
            } else {

                getUserAction(this)
                
            }
        } else {
            this.$router.push('/')
        }
    }
}
</script>

<style scoped>
    /* Parent-Dashboad */
    .dashboard {
    max-width: 900px;
    margin: 0 auto;
    }

    .loading-ct{
        background-color: #48483A;
        height: 91vh;
        max-width: 900px;
        margin: 0 auto;
    }

    .ct-spinner {
        height: 200px;
        width: 200px;
        color: rgb(117, 223, 69);
        margin-top: 60px;
    }
</style>