<template>
    <div class="col-md-8 ctc-2 pt-4 pb-2 uk-animation-slide-bottom-small">
        <h2 class="ct-color font-weight-normal">Welcome <span class="uk-text-white mr-auto"
            >{{userName}}!</span><div class="uk-text-meta">Todo List</div></h2>
        <hr>

        <div class="todo-list">
            <app-todos></app-todos>
        </div>

        <div id="modal-group-1" uk-modal>
            <div class="uk-modal-dialog">
                <button class="uk-modal-close-default" type="button" uk-close></button>
                <div class="uk-modal-header ct-modal-head">
                    <h2 class="uk-modal-title text-white">ADD TODO</h2>
                </div>
                <div class="uk-modal-body">
                    <div class="uk-margin">
                      <input class="uk-input" v-model="todo.title"
                        type="text" placeholder="Todo title">
                  </div>

                  <div class="uk-margin">
                    <textarea class="uk-textarea" rows="4" v-model="todo.body"
                      placeholder="Todo decription"></textarea>
                </div>
                </div>
                <div class="uk-modal-footer uk-text-right">
                    <button class="uk-button uk-button-default
                    uk-button-primary uk-modal-close" 
                    @click="addTodo"
                    type="button">Add</button>

                    
                </div>
            </div>
        </div>

        <button class="uk-button 
                uk-button-default 
                uk-width-1-1 shadow
                uk-margin-small-bottom 
                 ct-bg-btn"
                href="#modal-group-1"
                uk-toggle
                >Add Todo</button>
    </div>
</template>

<script>
    import Todos from '../todo/Todos.vue'
    import axios from 'axios'
    import { addTodoAction } from '../../../apis/additional.js'

    export default {
        data() {
          return {
            todo: {
              title: '',
              body: ''
            }
          }
        },
        computed: {
          userName() {
            return this.$store.state.user.name.split(" ")[0]
          }
        },
        components: {
            'app-todos': Todos
        },
        methods: {
          addTodo() {
            if(this.todo.title.length > 3 && this.todo.body.length > 3) {

              let todoOne = {
                title: this.todo.title,
                body: this.todo.body,
                time: Date.now()
              }

              addTodoAction(todoOne, this)

              this.todo.title = ''
              this.todo.body = ''
            } else {
              UIkit.notification({
                message: 'Please fill out the Todo fields', 
                status:'warning', 
                pos: 'bottom-center',
                timeout: 1000
              })
            }
          }
        }
    }
</script>

<style>
    .ctc-2 {
  background-color: #48483A;
  /* panel back --------------- color */
}

.ct-color {
    color: #F0F3BD !important;
}


/* Todo-list */
.todo-list {
  height: 400px;
  overflow: scroll;
  overflow-x: hidden;
}

.todo-list::-webkit-scrollbar {
  display: none;
}

.-todo-list {
  width: 0 !important;
}

/* Big Button */
/* Add Todo */
.ct-bg-btn {
  background: rgb(117, 223, 69);
  color: white;
  margin-top: 34px;
  border: none !important;
  transition: 0.2s;
}

.ct-bg-btn:hover {
  background-color: rgb(104, 196, 61);
  color: white;
  border: none !important;
}


/* Modal style */
.ct-modal-head {
  background-color: rgb(104, 196, 61);
  color: white;
}

</style>