<template>
    <div class="dashboard container">
        <h1 class="text-white pt-5"><span class="log">TODO</span> App</h1>
        <p class="add-color pl-4">Please add some more information</p>
        <div class="row">
            <div class="col-md-4 text-white pl-5">

                <div>
                    <div class="add-color">Location:</div>   
                    <div class="uk-margin">
                        <input class="uk-input uk-form-width-medium" v-model="location"
                        type="text" placeholder="Location">
                    </div>
                </div>

                <div>
                    <div class="add-color">Age:</div>   
                    <div class="uk-margin">
                        <input class="uk-input uk-form-width-medium" v-model="age"
                        type="text" placeholder="Age">
                    </div>
                </div>

                <router-link tag="button" to="/home" v-if="checkGoHome"
                    class="uk-button uk-button-default ct-btn-back
                    uk-button-small">Back to Dashboard</router-link>
            </div>
            <!-- <div class="col-md-2">
                <hr class="line text-white">
            </div> -->
            <div class="col-md-8">
                <p class="text-center add-color"
                    >Please select your avatar</p>
                <div class="uk-position-relative uk-visible-toggle uk-light" 
                tabindex="-1" uk-slideshow="min-height: 200; max-height: 300; 
                animation: push">

                    <ul class="uk-slideshow-items">
                        <li>
                            <img src="https://i.imgur.com/t0Iqni6.png%22%20alt=%22Commercial%20Photography"
                                class="profilePic" alt="pic" uk-cover>
                        </li>
                        <li>
                            <img src="https://i.imgur.com/EkuwPWI.png%22%20alt=%22Commercial%20Photography" 
                                class="profilePic" alt="pic" uk-cover>
                        </li>
                        <li>
                            <img src="https://i.imgur.com/6K6wGRI.png" 
                                class="profilePic" alt="pic" uk-cover>
                        </li>

                        <li>
                            <img src="https://i.imgur.com/rgXoBgL.png" 
                                class="profilePic" alt="" uk-cover>
                        </li>
                    </ul>

                    <a class="uk-position-center-left uk-position-small 
                        uk-hidden-hover" href="#" uk-slidenav-previous 
                        uk-slideshow-item="previous" @click="left"></a>
                    <a class="uk-position-center-right uk-position-small 
                        uk-hidden-hover" href="#" uk-slidenav-next 
                        @click="right"
                        uk-slideshow-item="next"></a>

                </div>
            </div>
        </div>

        <button class="uk-button 
                uk-button-default 
                uk-width-1-1 
                uk-margin-small-bottom
                ct-bg-btn"
                @click="editExecute"
                >Continue</button>
    </div>
</template>

<script>
import { editUserAction, getUserAction } from '../../apis/authentication'
import setAuthToken from '../../utils/setAuthToken.js'
import jwt_decode from 'jwt-decode'
import { logOutUser } from '../../apis/authentication.js'
import { checkLetter } from '../../apis/additional.js'

export default {
    data() {
        return {
            image: 1,
            age: this.$store.state.user.age,
            location: this.$store.state.user.location,
            imageArr: [
                'https://i.imgur.com/kwhtzwh.png%22%20alt=%22Commercial%20Photography',
                'https://i.imgur.com/DtwF3Xa.png" alt="Commercial Photography',
                'https://i.imgur.com/AQKYJHO.png%22%20alt=%22Commercial%20Photography',
                'https://i.imgur.com/oJoKF2q.png%22%20alt=%22Commercial%20Photography'
            ]
        }
    },
    computed: {
        checkGoHome() {
            return this.$store.state.user.infoUpdate
        }
    },
    methods: {
        right() {
            if (this.image === 4) {
                this.image = 1
                return 
            } 

            this.image++
        },
        left() {
            if (this.image === 1) {
                this.image = 4
                return 
            } 

            this.image--
        },
        editExecute() {
            let dataInfo1 = {
                avatar: this.imageArr[this.image - 1],
                age: this.age,
                location: this.location
            }

            if (typeof dataInfo1.age === 'number') {
                if (this.age && this.location.length > 2) {
                editUserAction(dataInfo1, this)
                return 
            }
            
            UIkit.notification({
                message: 'Please fill out the fields properly', 
                status:'warning', 
                pos: 'bottom-center',
                timeout: 1500
            })
            } else {

                let x = checkLetter(dataInfo1.age)

                if (this.age && this.location.length > 2 && !x) {
                    editUserAction(dataInfo1, this)
                    return 
                }
                
                UIkit.notification({
                    message: 'Please fill out the fields properly', 
                    status:'warning', 
                    pos: 'bottom-center',
                    timeout: 1500
                })

            }
        },
        goHome() {
            this.$router.push('/home')
        }
    },
    beforeMount() {
        if (localStorage.jwtToken) {
            setAuthToken(localStorage.jwtToken);
            const decoded = jwt_decode(localStorage.jwtToken);
            console.log("router secure...ggggg")

            const currentTime = Date.now() / 1000;
            if (decoded.exp < currentTime) {
            // Logout user
            console.log("logging out user...")
            logOutUser(this)
            this.$router.push('/')
            } else {

                getUserAction(this)
                
            }
        } else {
            this.$router.push('/')
        }
    }
}
</script>

<style scoped>

.dashboard {
max-width: 900px;
height: 91.5vh;
margin: 0 auto; 
background-color: rgb(63, 63, 63);
}
.log{
    color: rgb(63, 231, 63);
}
/* 
back button */
.ct-btn-back {
    background-color: rgb(56, 187, 67);
    margin-top: 20px;
    border: 0;
    color: white
}

/* 
Photo Slider */
.profilePic {
    height: auto;
    width: 40%;
}

/* Add Todo */
.ct-bg-btn {
  background: linear-gradient(to right, rgb(117, 223, 69) , rgb(96, 216, 116));
  /* max-width: 400px; */
  color: white;
  border: none !important;
  margin-top: 70px;
}

.ct-bg-btn:hover {
  background-color: rgb(104, 196, 61);
  color: white;
  border: none !important;
}


input[type=text] {
    background-color: #757171;
    border: 0;
    color: #dbc9c9;
    border-radius: 2px;
}

.add-color {
    color: rgb(197, 196, 196);
}
</style>