<template>
    <div class="dashboard container uk-animation-slide-bottom-medium">
        <h1 class="text-center pt-5 
        display-2 mb-4 ct-heading">Your <span class="todo-t">TODO</span> App!</h1>
    
        <div class="card mx-auto shadow-lg ct-card text-center"
            :class="{'animate-me': !checkSign, 'animate-me-small': checkSign}"
             style="width: 16rem;">
            <div class="card-body"
                :class="{'uk-animation-slide-bottom-medium': checkSign}"
                 v-if="checkSign">
                <h5 class="card-title text-center ct-head">
                    <span class="add-color">Login</span>
                </h5>
                <div class="uk-margin">
                    <input class="uk-input uk-form-width-medium shadow-sm" 
                    v-model="login.email" 
                    type="text" placeholder="Email">
                </div>

                <div class="uk-margin">
                    <input class="uk-input uk-form-width-medium Small shadow-sm"
                    v-model="login.password" 
                    type="text" placeholder="Password">
                </div>

                 <button class="uk-button
                    uk-button-default ct-log-btn shadow-sm"
                        @click="loginAction">Login</button>
                    <p class="ct-sign"
                    >Don't have an account? <span class="ct-sign-t" @click="loginStatus">Sign Up</span></p>
            </div>

            <div class="card-body"
                :class="{'uk-animation-slide-top-medium': !checkSign}"
                 v-else>
                <h5 class="card-title text-center ct-head">
                    <span class="add-color">Sign up</span></h5>

                <div class="uk-margin">
                    <input class="uk-input uk-form-width-medium" 
                    v-model="signup.name" 
                    type="text" placeholder="Full Name">
                </div>

                <div class="uk-margin">
                    <input class="uk-input uk-form-width-medium" 
                    v-model="signup.email" 
                    type="text" placeholder="Email">
                </div>

                <div class="uk-margin">
                    <input class="uk-input uk-form-width-medium" 
                    v-model="signup.password" 
                    type="text" placeholder="Password">
                </div>
                

                 <button class="uk-button
                    uk-button-default ct-log-btn shadow-sm"
                    @click="signUpExec">Sign Up</button>
                    <p class="ct-sign"
                    >Do have an account? <span class="ct-sign-t" 
                        @click="loginStatus">Login</span></p>
            </div>
        </div>
       
    </div>
</template>

<script>
    import { signUpAction, loginUserAction } from '../apis/authentication.js'

export default {
    data() {
        return {
            input1: false,
            signUp: true,
            login: {
                email: '',
                password: ''
            },
            signup: {
                name: '',
                email: '',
                password: ''
            }
        }
    },
    computed: {
        checkSign() {
            return !this.$store.state.login.log
        }
    },
    methods: {
        loginStatus() {
            this.$store.state.login.log = !this.$store.state.login.log
        },
        loginAction() {
            let logind = {
                email: this.login.email,
                password: this.login.password
             }

            if (this.login.email.length > 3
                 && this.login.password.length > 3) {
                
                loginUserAction(logind,this)
                
            } else {
                UIkit.notification({
                    message: 'Please fill out the fields', 
                    status:'warning', 
                    pos: 'bottom-center',
                    timeout: 1000
                })
            }
        },
        signUpExec() {
            if (this.signup.email.length > 3 
                && this.signup.password.length > 3
                && this.signup.email.length > 3) {

                let dataInfo = {
                    name: this.signup.name,
                    email: this.signup.email,
                    password: this.signup.password
                }

                signUpAction(dataInfo, this)
            } else {
                UIkit.notification({
                    message: 'Please fill out the fields', 
                    status:'warning', 
                    pos: 'bottom-center',
                    timeout: 1000
                })
            }
        }
    }
}
</script>

<style scoped>

/* Parent-Dashboad */
    .dashboard {
    max-width: 900px;
    height: 91.5vh;
    margin: 0 auto; 
    background-color: #3d3a3a;
    }

    .ct-heading {
        color: rgb(230, 229, 216);
    }


/* Login Card */
.ct-card {
    background: rgb(75, 74, 74);
    border: 0;
    border-radius: 8px;
    padding: 0;
    border-radius: 4px;
}
/* Animation style */
.animate-me {
   animation-name: expan;
   animation-duration: 0.5s;
}

@keyframes expan {
    from{ height: 282px;}
    to{  height: 348px; }
}

.animate-me-small {
   animation-name: sink;
   animation-duration: 0.5s;
}


@keyframes sink {
    from{ height: 342px;}
    to{  height: 288px; }
}


.ct-head {
    font-size: 30px;
    font-weight: 300; 
    color: white;
}

.todo-t {
    background: -webkit-linear-gradient(rgb(93, 226, 93), rgb(69, 161, 74));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    /* background-color: rgb(107, 195, 66) */
}

/* Buttton-------- */
.ct-log-btn {
    background-color: rgb(107, 195, 66);
    border: 0;
    color: rgb(227, 228, 188);
    border-radius: 2px;
    transition: 0.2s;
}

.add-color {
    color: rgb(165, 165, 165);
}

.ct-log-btn:hover {
    border: 0;
    color: white;
    transform: translateY(-2px);
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.15)!important;
}

.ct-log-btn:active {
    transform: translateY(0);
    box-shadow: 0;
}

/* Footer */
.ct-sign {
    font-size: 12px;
    color: rgb(209, 209, 208);
    transform: translateY(18px);
}

.ct-sign-t {
    color: rgb(117, 223, 69);
    cursor: pointer;
}

/* Input area style */
input[type=text] {
    background-color: #757171;
    border: 0;
    color: #ccbdbd;
    border-radius: 2px;
    transition: 0.4s;
}

input:focus {
    transform: scale(1.1);
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.15)!important;
}

/* 
Notification  */
.uk-notification-message-warning {
    background: red !important;
}

@media only screen and (max-width: 765px) {
    .ct-heading {
        font-size: 40px;
    }

    .ct-head {
        font-size: 25px;
    }
}

@media only screen and (max-width: 332px) {
    .ct-heading {
        font-size: 30px;
    }

    .ct-head {
        font-size: 20px;
    }
}
</style>