<template>
  <nav class="navbar navbar-expand-lg navbar-dark ct-nav ct-nav-b-s shadow uk-animation-slide-top-medium">
    <a class="navbar-brand" href="#">
      <img src="../assets/image/Asset 1.png" class="logo"></a>
      <button class="navbar-toggler" type="button" 
        data-toggle="collapse" data-target="#navbarNav" 
        aria-controls="navbarNav" aria-expanded="false" 
        aria-label="Toggle navigation">
      <span uk-icon="icon: menu; ratio: 1.2"></span>
    </button>
    <div class="collapse navbar-collapse" id="navbarNav">
      <ul class="navbar-nav">
        <li class="nav-item active">
          <router-link to="/home">
          <a class="nav-link" v-if="check" 
          data-toggle="collapse" data-target="#navbarNav"
          >Home <span class="sr-only">(current)</span></a></router-link>
        </li>
        <li class="nav-item" v-if="check">
          <a class="nav-link" @click="logOut" 
          data-toggle="collapse" data-target="#navbarNav">Sign out</a>
        </li>
        <li class="nav-item" v-else>
          <a class="nav-link" v-if="loginScreen" @click="statusChange"
            data-toggle="collapse" data-target="#navbarNav" >Login</a>
          <a class="nav-link" 
            v-else-if="!loginScreen" @click="statusChange"
            data-toggle="collapse" data-target="#navbarNav">Sign up</a>
        </li>
      </ul>
    </div>
</nav>
</template>

<script>
import { logOutUser } from '../apis/authentication.js'

export default {
  computed: {
    check() {
      return this.$store.state.user.email.length > 2
    },
    loginScreen() {
      return this.$store.state.login.log
    }
  },
  methods: {
    logOut() {
      logOutUser(this)
    },
    statusChange() {
    this.$store.state.login.log = !this.$store.state.login.log
    }
  }
}
</script>

