<template>
    <div class="uk-child-width-1-2@s uk-grid-match" uk-grid>
        <span class="uk-margin-small-right text-center ct-spinner uk-position-center" 
            uk-spinner="ratio: 3" v-if="checkLoad" ></span>
        <app-todo v-for="todo in todos"
             :key="todo._id"
             :todo="todo" v-else></app-todo>   
    </div>
</template>

<script>
    import Todo from './Todo.vue'

export default {
    computed: {
        todos() {
            return this.$store.state.todos
        },
        checkLoad() {
            return this.$store.state.loading1
        }
    },
    components: {
        'app-todo': Todo 
    }
}

</script>

<style scoped>
    .ct-spinner {
        height: 150px;
        width: 150px;
        color: rgb(117, 223, 69);
        
    }
</style>

