<template>
    <div>
        <div class="uk-card
            uk-card-default
            uk-card-hover
            ct-card
            d-flex flex-column justify-content-between">
            <div>
                <h3 class="uk-card-title 
                    ct-card-heading  
                    d-flex
                    justify-content-between
                    ">
                    <div class="font-weight-normal">{{todo.title}}
                        <div class="uk-text-meta">{{getTime}}</div>
                    </div>
                    <a @click="completed" uk-icon="close" class="close-tran"></a>
                </h3>
                <p>{{todo.body}}</p>
            </div>


            <div id="modal-group-5" uk-modal>
            <div class="uk-modal-dialog">
                <button class="uk-modal-close-default" type="button" uk-close></button>
                <div class="uk-modal-header ct-modal-head-edit uk-background-primary">
                    <h2 class="uk-modal-title text-white">EDIT</h2>
                </div>
                <div class="uk-modal-body">
                    <div class="uk-margin">
                      <input class="uk-input" v-model="etitle"
                        type="text" placeholder="Todo title">
                  </div>

                  <div class="uk-margin">
                    <textarea class="uk-textarea" rows="4" v-model="ebody"
                      placeholder="Todo decription"></textarea>
                </div>
                </div>
                <div class="uk-modal-footer uk-text-right">
                    <button class="uk-button uk-button-default
                    uk-button-primary uk-modal-close" 
                    @click="editTodo"
                    type="button">Done</button>

                    
                      </div>
                  </div>
              </div>

            <div class="d-flex justify-content-between">
                <button class="btn ct-btn" @click="completed">
                    Completed</button>
                <button class="btn ct-btn-2" href="#modal-group-5"
                uk-toggle>Edit</button>
            </div>
        </div>

    </div>
</template>

<script>
  import moment from 'moment'
  import axios from 'axios'
  import { 
      deleteTodoAction, 
      editTodoAction
    } from '../../../apis/additional.js'

export default {
  props: ['todo'],
  data() {
    return {
      etitle: this.todo.title,
      ebody: this.todo.body
    }
  },
  computed: {
    getTime() {
      return moment(this.todo.time).fromNow()
    }
  },
  methods: {
    completed(){
      deleteTodoAction(this.todo._id, this)
    },
    editTodo() {
      const todoInfoEdit = {title: this.etitle, body: this.ebody}
      if (todoInfoEdit.title.length > 2 && todoInfoEdit.body.length > 2)
      editTodoAction(this.todo._id, this, todoInfoEdit)
    }
  }
}
</script>


<style>
    /* New card custom */
.ct-card {
  padding: 25px;
  border-radius: 4px;
}

.ct-card p {
  font-size: 14px;
}

.ct-card-heading {
  font-size: 18px;
}

.ct-card-heading {
  font-size: 18px;
}

.close-tran {
  transform: translateY(2px)
}

/* Button style */
/* Style Button */
.ct-btn {
  background-color: rgb(117, 223, 69);
  color: white;
  width: 100px;
  font-size: 15px;
  border-radius: 2px;
  transition: .2s;
}

.ct-btn-2 {
  width: 70px;
  font-size: 15px;
  border-radius: 2px;
  background-color: rgb(177, 176, 176);
  color: white;
}

.ct-btn-2:hover {
  color: white;
  background-color: rgb(128, 128, 128);
}

.ct-btn:hover {
  background-color: rgb(95, 184, 54);
  color: white;
}

/* close btn */
.close-btn {
  color: red;
  cursor: pointer;
}

/* Label box */
.label-box {
  background-color: rgb(27, 91, 240);
  height: 3px;
  width: 100%;
  border-radius: 4px;
}
</style>

