import axios from 'axios'

export const addTodoAction = (data, obj) => {
    loadingOne(true, obj)
    axios.post('/api/users/todo/add', data)
        .then(result => {
            obj.$store.state.todos = result.data
            loadingOne(false, obj)
        })
        .catch(err => {
            console.log("Error add todo action: ", err)
        })
}

export const loadingOne = (val, obj) => {
    obj.$store.state.loading1 = val
}

export const deleteTodoAction = (id, obj) => {
    loadingOne(true, obj)
    axios.delete(`/api/users/todo/delete/${id}`)
        .then(result => {
            obj.$store.state.todos = result.data
            loadingOne(false, obj)
        })
        .catch(err => {
            console.log("Error delete todo action: ", err)
        })
}

export const editTodoAction = (id, obj, data) => {
    loadingOne(true, obj)
    axios.post(`/api/users/todo/edit/${id}`,data)
        .then(result => {
            console.log("edit todo: ", result.data)
            obj.$store.state.todos = result.data
            loadingOne(false, obj)
        })
        .catch(err => {
            console.log("Error edit todo action: ", err)
        })
}

export const checkLetter = (word) => {
    let checkMe
    word.split("").forEach(letter => {
        let eng = "wertyuiop[]\asdfghjkl;zxcvbnm,./'"
        let val = eng.split("").includes(letter)
        if (val) {
            checkMe = val
        }
    });

    if (checkMe) {
        return true
    } else {
        return false
    }
}